body {
  background-color: orange;
}

h1 {
  text-align: center;
  letter-spacing: 3px;
}

.inp_cmp {
  padding: 30px;
}

.inp {
  font-size: large;
  padding: 5px;
  border: none;
  border-radius: 4px;
}

.add_btn {
  font-size: large;
  padding: 5px;
  border: none;
  border-radius: 4px;
  margin-left: 10px;
  cursor: pointer;
}

.center {
  width: 1000px;
  margin: 0 auto;
}

.fc {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.select {
  padding: 30px;
}

.select select {
  font-size: large;
  padding: 5px;
  width: 300px;
}

/*List*/
.listContainer {
  padding: 100px;
  text-align: center;
}

.resultInp {
  font-size: large;
  padding: 5px;
  border: none;
}

.btn {
  border: none;
  font-size: large;
  padding: 5px;
  width: 35px;
  cursor: pointer;
}

.comp {
  background-color: green;
}

.del {
  background-color: orangered;
}

.listContainer section {
  margin-top: 10px;
}

.line {
  text-decoration: line-through;
}